<template>
    <div style="width : 65%">
        <a-typography-title class="title">
            Edit Deck
        </a-typography-title>
        <a-form :model="form" :layout="layout" class="form-box">
            <a-form-item field="search" label="search">
                <a-input v-model="form.search" placeholder="input deck name" />
            </a-form-item>

            <a-form-item>
                <a-button @click="searchMethod">Submit</a-button>
            </a-form-item>
        </a-form>
    </div>
    <div>
        <a-table :columns="columns" :data="data" />
    </div>
    <div>
        <a-form :model="form" :layout="layout" class="form-box">
            <a-form-item field="id" label="Row Id">
                <a-input v-model="form.id" placeholder="Enter id of the row you want to edit" />
            </a-form-item>
            <a-form-item field="word" label="Word">
                <a-input v-model="form.word" placeholder="Edit word" />
            </a-form-item>
            <a-form-item field="id" label="Row Id">
                <a-input v-model="form.meaning" placeholder="Edit meaning" />
            </a-form-item>

            <a-form-item>
                <a-button @click="EditMethod">Submit</a-button>
            </a-form-item>
        </a-form>
    </div>
</template>

<script>

import { inject, ref, reactive } from 'vue'
import { Modal } from '@arco-design/web-vue';


export default {
    name: 'EditComponent',
    setup() {
        const layout = ref('vertical')
        const form = reactive({});
        const $axios = inject("$axios");
        const columns = [
            {
                title: 'id',
                dataIndex: 'ids',
            },
            {
                title: 'word',
                dataIndex: "words"
            },
            {
                title: 'meaning',
                dataIndex: 'meanings',
            },

        ];

        var data = reactive([]);


        const word = ref();
        const meaning = ref();

        const searchMethod = () => {
            $axios({
                url: "/anki/searchDecks.do",
                method: "post",
                data: "deck_name=" + form.search,
                headers: {}
            }).then((res) => {
                if (res.data.status == 200) {
                    console.log(res);
                    var tempdata = res.data.data;
                    console.log(tempdata);
                    for (var i = 0; i < tempdata.length; i++) {
                        var obj = tempdata[i];
                        obj.ids = obj.id;
                        obj.words = obj.word;
                        obj.meanings = obj.meaning;
                        data.push(obj);
                    }
                } else {
                    Modal.error({
                        title: 'Error',
                        content: res.data.info
                    });
                }
            })
        }

        const EditMethod = () => {
            console.log(word.value);
            console.log(meaning.value);

            $axios({
                url: "/anki/editDecks.do",
                method: "post",
                data: "id="+ form.id + "&word=" + form.word + "&meaning=" + form.meaning,
                headers: {

                }
            }).then((res) => {
                console.log(res);
                console.log("The word is"+form.word)
                console.log("The meaning is"+form.meaning)
                if (res.data.status == 200) {
                    Modal.success({
                        title: 'Success',
                        content: res.data.info
                    });
                } else {
                    Modal.error({
                        title: 'Error',
                        content: res.data.info
                    });
                }
            })
        }

        return {
            layout,
            form,
            data,
            columns,
            EditMethod,
            searchMethod,
            word,
            meaning
        }
    }
}
</script>

<style>
.title {
    margin-left: 2%;
    text-align: left;
}

/* .arco-layout-content {
    display: inline;
} */

.form-box {
    margin-left: 2%;
    text-align: left;
}
</style>