            },
            {
                title: ' Words Got Wrong',
                dataIndex: 'wrong_words',
            },
  
            {
                title: ' Weaknesses',
                dataIndex: 'wrong_meanings',
            },
  
  
        ];

        var data = reactive([]);
        
        onMounted(()=> {
            $axios({
                url:"/anki/results.do",
                method:"post",
               
                headers : {}
            }).then((res)=>{
                var tempdata = res.data.data;
   //             var correctArray=tempdata.RightAnswers
                console.log(tempdata);
   //             for(var i = 0; i < tempdata.length; i++) {
    //                var obj = tempdata[i];
    //                obj.correct_words = obj.RightAnswers;
     //               obj.Correct_meanings=obj.RightMeaning;
       //             obj.wrong_words=obj.WrongAnswers;
         //           obj.wrong_meanings=obj.WrongMeaning;
           //         console.log("words got right" +obj.correct_words );
             //       console.log("meanings for the right words" +obj.Correct_meanings);
               //     console.log("words got wrong"+obj.wrong_words);
                 //   console.log("meanings of words got wrong"+obj.wrong_meanings);
                   // data.push(obj);
   //             }
                var wrongArray=tempdata.WrongAnswers
                console.log(wrongArray)
                for(var x=0; x<wrongArray.length; x++){
                    var wrongObj=wrongArray[x];
                    wrongObj.wrong_words=wrongObj.RightAnswers;
                    data.push(wrongObj);
                }
            })
        });
        
        return {
            columns,
            data
        }
    },
}
</script>

