import { createRouter, createWebHistory } from 'vue-router'
import LoginComponent from '../components/LoginComponent.vue'
import SignUpComponent from '../components/SignUpComponent.vue'
//import ForgotPasswordComponent from '../components/ForgotPasswordComponent.vue'

import IndexView from '../views/IndexView.vue'
import ShowDeckComponent from '../components/ShowDeckComponent.vue'
import CreateDeckComponent from '../components/CreateDeckComponent.vue'
import AddToExistingDeckComponent from '../components/AddToExistingDeckComponent.vue'
import EditComponent from '../components/EditComponent.vue'
import ResetPasswordComponent from '../components/ResetPasswordComponent.vue'
import DeleteComponent from '../components/DeleteComponent.vue'
import NormalTestComponent from '../components/NormalTestComponent.vue'
import ResultsComponent from '../components/ResultsComponent.vue'

const routes = [
  {
    path: "/home",
    name:"index",
    component : IndexView,
    children: [
      {
        path:"/home/normalTest",
        name:"normalTest",
        component:NormalTestComponent
      },
      {
        path:"/home/results",
        name:"results",
        component:ResultsComponent
      },
      
      
      {
        path:"/home/edit",
        name:"edit",
        component:EditComponent
      },
      {
        path:"/home/delete",
        name:"delete",
        component:DeleteComponent
      },
      
      {
        path:"/home/AddDecks",
        name:"AddDecks",
        component: AddToExistingDeckComponent
      },
      {
        path:"/home/ShowDecks",
        name:"ShowDecks",
        component: ShowDeckComponent
      },

      {
        path:"/home/CreateDeck",
        name:"CreateDeck",
        component: CreateDeckComponent
      },

    ]},
  {
    path: "/",
    name: "login",
    component: LoginComponent,
  },

      {
        path:"/signUp",
        name:"signUp",
        component:SignUpComponent
      },
      {
        path:"/forgotPassword",
        name:"forgotPassword",
        component:()=> import("@/components/ForgotPasswordComponent.vue")
      },
      {
        path:"/reset",
        name:"reset",
        component:ResetPasswordComponent
      },
      
  
 
 



]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
