<template>

    <div style="width : 65%">
        <a-typography-title class="title">
            Normal Test
            <h2>Input deck name to begin test..</h2>
        </a-typography-title>
        <a-form :model="form" :layout="layout" class="form-box">
            <a-form-item field="search" label="search">
                <a-input v-model="form.search" placeholder="input deck name" />
            </a-form-item>

            <a-form-item>
                <a-button @click="searchMethod">Next card</a-button>
            </a-form-item>
        </a-form>
    </div>

    <div>

        <a-card hoverable :style="{ width: '360px', marginBottom: '20px' }">
            <div :style="{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'space-between',
            }">
                <span :style="{ display: 'flex', alignItems: 'center', color: '#1D2129' }">
                    <a-avatar :style="{ marginRight: '8px', backgroundColor: '#165DFF' }" :size="28">
                        Easy
                    </a-avatar>
                    <a-typography-text>{{ word }}</a-typography-text>
                </span>

            </div>
        </a-card>

    </div>

    <div style="width : 360px">

        <a-form :model="form" :layout="layout" class="form-box">
            <a-form-item field="answer" label="answer">
                <a-input v-model="form.answer" placeholder="input your answer" />
            </a-form-item>

            <a-form-item>
                <a-button @click="answerMethod">Ok</a-button>
            </a-form-item>
            <a-form-item>
                <a-button @click="ResultsMethod">Stop</a-button>
            </a-form-item>
        </a-form>
    </div>
</template>
<script>

import { reactive, inject, onMounted, ref } from 'vue'
import { Modal } from '@arco-design/web-vue';
export default {
    setup() {
        const word = ref();
        const layout = ref('vertical')
        const form = reactive({});
        const $axios = inject("$axios");
        const $router = inject("$router");

        onMounted(() => {
            $axios({
                url: "",
                method: "post",
                headers: {}
            }).then((res) => {
                console.log(res);
                word.value = res.data.data.word;

            })
        })
        const searchMethod = () => {
            $axios({
                url: "/anki/normalTest.do",
                method: "post",
                data: "deck_name=" + form.search,
                headers: {}
            }).then((res) => {
                if (res.data.status == 200) {
                    console.log(res);
                    word.value = res.data.data.word;

                } else {
                    Modal.error({
                        title: 'Error',
                        content: res.data.info
                    });
                }
            })
        }

        const answerMethod = () => {


            $axios({
                url: "/anki/answerHandler.do",
                method: "post",
                data: "meaning=" + form.answer,
                headers: {

                }
            }).then((res) => {
                console.log(res);
                if (res.data.status == 200) {
                    Modal.success({
                        title:"Congratualtions!",
                        content: "You got it right wanna continue?"
                    })

                } else {
                    Modal.error({
                        title: "Boo hoo, you're wrong",
                        content: "wanna continue?"
                    });
                }
                $router.push("/home/normalTest")
            })
        }

        const ResultsMethod = () => {

            $router.push("/home/results")

        }


        return {
            word,
            form,
            layout,
            ResultsMethod,
            answerMethod,
            searchMethod
        }
    },
}
</script>
  
<style scoped>
.infor-form {
    width: 100%;
    height: 100%;
}
</style>