<template>
    <div>
        <a-table :columns="columns"  :data="data"/>
    </div>
</template>

<script>
import { inject, onMounted, reactive } from 'vue';

export default {
    setup() {
        const $axios = inject("$axios")
        const columns = [
            {
                title: 'Deck Name',
                dataIndex: 'deckName',
            },
            {
                title : 'word',
                dataIndex : "words"
            },
            {
                title: 'meaning',
                dataIndex: 'meanings',
            },
  
        ];

        var data = reactive([]);
        
        onMounted(()=> {
            $axios({
                url:"/anki/showDecks.do",
                method:"post",
               
                headers : {}
            }).then((res)=>{
                var tempdata = res.data.data;
                console.log(tempdata);
                for(var i = 0; i < tempdata.length; i++) {
                    var obj = tempdata[i];
                    obj.deckName = obj.deck_name;
                    obj.words=obj.word;
                    obj.meanings=obj.meaning;
                    data.push(obj);
                }
            })
        });
        
        return {
            columns,
            data
        }
    },
}
</script>

